* {
    margin: 0;
    padding: 0;
}

body{
    width: 100%;
    height: 100vh;
    font-family: 'Futura BK BT', sans-serif;
}


.carousel{
    position: relative;
    width: 100%;
    height: 100%;
}

.carouselInner{
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
}

.carouselInner .left{
    flex: 10%;
    height: 100%;
    display: grid;
    place-items: center;
    opacity: 0%;
}

.carouselInner .center{
    flex: 50%;
    height: 100%;
}

.carouselInner .right{
    flex: 5%;
    height: 100%;
    display: grid;
    place-items: center;
    opacity: 0%;
}


.left:hover{
    opacity: 100%;
}

.right:hover{
    opacity: 100%;
}

.header{
    position: absolute;
    height: 10%;
    width: 100%;
    display: flex;
    padding-left: 15vw;
    margin: 0;
}

.header img{
    fill:#016795;
}

.container{
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    font-size: 3vh;
}

.textMenu{
    position: relative;
    margin: auto 1vw;
}

.header img{
    position: relative;
    height: 100%;
    width: 25%;
    margin-top: auto;
    transform: scale(0.7);
}

.logo{
    position: relative;
    height: 100%;
    width: 30%;
    
}
.logo svg{
    position: relative;
    transform: scale(0.5);
    height: 10vh;
    width: 30vw;
}

.container_logo{
    position: absolute;
    margin-top: 5%;
    height: 30vh;
    width: 100%;
}


.content01{
    position: absolute;
    height: 40%;
    width: 100%;
    display: flex;
    margin: auto;
    margin-top: 35vh;
}

.text_content01{
    position: relative;
    height: 100%;
    width: 85%;
    margin-left: 23vw;
    font-size: 13px;
    margin-right: 10vw;
    margin-top: 5vh;
}

.img_content01{
    position: relative;
    height: 110%;
    width: 100%;
    padding-right: 20vw;
    background-image: linear-gradient(to bottom left, #ff073a, #016795);
    border-top-left-radius: 200%;
    border-bottom-left-radius: 10%;
}

.img_content01 img{
    transform: scale(1.5);
}

.content02{
    position: absolute;
    height: 70%;
    width: 100%;
    display: flex;
    margin: auto;
    margin-top: 90vh;
}

.img_content02{
    position: relative;
    height: 50vh;
    width: 20vw;
    margin: auto;
}

.img_content02 div{
    position: relative;
    width: 100%;
}

.img_content02 div h1{
    font-size: 5vh;
    height: 100%;
    width: 100%;
    text-align: center;
}

.img_content02 div p{
    font-size: 3vh;
    height: 100%;
    width: 100%;
    text-align: center;
}

mark {
    background-color:#ff073a;
}


.navigator{
    position: absolute;
    height: 15vh;
    width: 100%;
    margin-top: 200vh;
}

.nav_content {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    padding: 5vh;
}

.nav_content svg{
    position: relative;
    height: 30px;
}


.slider{
    position: absolute;
    margin-top: 30vh;
    height: 100%;
    width: 100%;
}

.checkbox {
	display: none;
}

.label {
	border-radius: 50px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 5px;
	position: relative;
	height: 25px;
	width: 52px;
	transform: scale(1.3);
    margin: auto 2vw;
}

.label .ball {
    border-radius: 50%;
	position: absolute;
	height: 17px;
	width: 17px;
	transform: translateX(0px);
	transition: transform 0.2s linear;
    z-index: 1;
}

.checkbox:checked + .label .ball {
    transform: translateX(25px);
}

.material-symbols-outlined {
    position: relative;
    transform: scale(0.5);
    right: 7px;
    bottom: 5px;
    margin: auto 0.2vw;
    z-index: 0;
}

span{
    cursor: pointer;
}

.switch {
    visibility: hidden;
    position: absolute;
    margin-left: -9999px;
}
  
.switch + label {
    display: block;
    position: relative;
    cursor: pointer;
    outline: none;
    user-select: none;
}


.switch--container{
    position: relative;
    margin: auto 1vw;
}

.switch--shadow + label {
    padding: 2px;
    width: 50px;
    height: 20px;
    border-radius: 60px;
}
.switch--shadow + label:before,
.switch--shadow + label:after {
    display: block;
    position: absolute;
    top: 1px;
    left: 1px;
    bottom: 1px;
    content: "";
}
.switch--shadow + label:before {
    right: 1px;
    border-radius: 60px;
}
.switch--shadow + label:after {
    width: 50px;
    border-radius: 100%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: all 0.4s;
}
.switch--shadow:checked + label:after {
    transform: translateX(26px);
}

::-webkit-scrollbar {
    width: 0.5vw;
    height: 0.5vh;
}

::-webkit-scrollbar-track {
background: #1b2431; 
}

::-webkit-scrollbar-thumb {
background: #1e488f; 
}

::-webkit-scrollbar-thumb:hover {
background: #FF073A; 
}

.sidebar{
    position: absolute;
    height: 100%;
    width: 200px;
    background-color: #1b2431;
}

.sidebarList{
    height: 100%;
    width: 100%;
}

.sidebarList .row:hover{
    width: 100%;
    height: 50px;
    background-color: #ff073a;
}

@media (max-width: 412px){
    .header{
        position: absolute;
        height: 100%;
        width: 50%;
        display: none;
        padding-left: 0vw;
    }

    .logo{
        position: relative;
        height: 10%;
    }
    .logo svg {
        transform: scale(0.8);
        height: 5vh;
        width: 30vw;
    }
    
    .textMenu{
        margin: 5vw;
        width: 50%;
    }

    .iconMenu{
        position: absolute;
    }

    .switch--container {
        margin: 5vw;
        width: 50%;
    }

    .content01{
        display: block;
        margin-top: 10vh;
        height: 50vh;
    }

    .text_content01 {
        position: relative;
        text-align: center;
        height: 50%;
        width: 80%;
        margin: auto;
        font-size: 15px;
    }

    .img_content01 {
        position: relative;
        height: 80%;
        width: 100%;
        background-image: linear-gradient(to bottom left, #ff073a, #016795);
        border-radius: 60%;
        padding-right: 0;
    }


    .img_content01 img {
        position: relative;
        width: 100%;
        height: 100%;
        transform: scale(0.9);
    }

    .content02 {
        position: absolute;
        height: 70%;
        width: 100%;
        display: flex;
        margin: auto;
        margin-top: 65vh;
    }

    .img_content02 {
        position: relative;
        height: 30vh;
        width: 40vw;
        margin: auto;
    }

    .img_content02 div h1 {
        font-size: 3vh;
        height: 100%;
        width: 100%;
        text-align: center;
    }
    
    .img_content02 div p {
        font-size: 2vh;
        height: 100%;
        width: 100%;
        text-align: center;
    }

    .navigator {
        position: absolute;
        height: 10vh;
        width: 100%;
        margin-top: 140vh;
    }
}